html,body {
    height: 100%;
    color: #000;
    font-family: sans-serif;
    font-size: 32px;
    background: #0f0c29;;
    background: -webkit-linear-gradient(to right, #0f0c29, #302b63, #24243e);
    background: -webkit-gradient(linear, left top, right top, from(#0f0c29), color-stop(#302b63), to(#24243e));
    background: -webkit-linear-gradient(left, #0f0c29, #302b63, #24243e);
    background: linear-gradient(to right, #0f0c29, #302b63, #24243e);
}

.wrapper,
main {
    width: 100%;
}

.content {
    width: 100%;
    max-width: 1430px;
    margin: 0 auto;
}

.inner {
    padding: 15px;
    text-align: center;
}

.title,
.subtitle {
  padding-bottom: 15px;
  font-size: 45px;
  color: #ffd700;
  font-weight: 700;
  text-transform: uppercase;
}

.subtitle {
    position: relative;
    padding: 15px 0;
    font-size: 35px;
}
.subtitle::after,
.subtitle::before {
    content: '';
    display: block;
    width: 15%;
    height: 2px;
    position: absolute;
    top: 50%;
    background: #ffd700;
}

.subtitle::after {
    left: 25%;
}

.subtitle::before {
    right: 25%;
}
      /* calendar nav */

.calendar-nav {
  padding: 25px 0;
  width: 100%;
  max-width: 100%;
  display: flex;
  justify-content: space-evenly;
  background-color: rgba(255,255, 255,.05);
  border-radius: 100px;
}

.calendar-nav-btn {
  background: none;
  border: none;
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 24px;
}
.calendar-nav-btn:hover {
    color: #ffd700;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition: .8s;
    transition: .8s;
}
#calendar {
    padding: 15px;
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    color: white;
    background-color: rgba(255,255, 255,.05);
    border-radius: 10px;
}

.weekDay {
    padding: 5px 0 25px 0;
    cursor: default;
}

.day {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px 0;
}

.currentDay {
    padding: 0;
}

.date {
    cursor: pointer;
}

.date:hover {
    color: #ffd700;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    -webkit-transition: .8s;
    transition: .8s;
}

.currentDate {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 64px;
    height: 60px;
    background: #ffd700;
    border-radius: 10px;
    font-weight: 700;
}

.currentDate:hover {
    color: #fff;
    -webkit-transition: .8s;
    transition: .8s;
}
@media(max-width: 1240px) {
    .subtitle::after {
        left: 20%;
    }
    
    .subtitle::before {
        right: 20%;
    }
}

@media(max-width: 860px) {
    .subtitle::after {
        left: 18%;
    }
    
    .subtitle::before {
        right: 18%;
    }
}

@media(max-width: 720px) {
    body {
        font-size: 24px;
    }

    .title {
        font-size: 35px;
    }

    .calendar-nav-btn {
        font-size: 18px;
    }

    .subtitle {
        font-size: 24px;
    }

    .currentDate {
        width: 47px;
        height: 44px;
        border-radius: 5px;
    }
}

@media(max-width: 480px) {
    body,
    .subtitle {
        font-size: 18px;
    }

    .title {
        font-size: 25px;
    }

    .subtitle::after {
        left: 15%;
    }
    
    .subtitle::before {
        right: 15%;
    }
    #calendar {
        border-radius: 5px;
    }
    
    .calendar-nav {
        padding: 15px 0;
    }

    .calendar-nav-btn {
        font-size: 14px;
    }

    .weekDay {
        padding-bottom: 15px;
    }

    .day {
        padding: 10px 0;
    }

    .currentDay {
        padding: 0;
    }
    .currentDate {
        width: 35px;
        height: 33px;
    }
}

@media(max-width: 320px) {
    .calendar-nav-btn {
        font-size: 12px;
    }

    .subtitle::after {
        left: 12%;
    }

    .subtitle::before {
        right: 12%;
    }
    
}
